.container {
  background-color: #06283d;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}
.container {
  box-shadow: 0px 0px 10px #1363df;
  border-radius: 8px;
  padding: 1rem;
  background-color: #dff6ff;
}
input {
  width: 100%;
  font-size: 1.2rem;
  padding: 8px 4px;
  margin: 0px 0;
  border-radius: 8px;
}
.btn {
  display: block;
  width: 100%;
  font: size 1.2rem;
  margin-top: 20px;
  padding: 10px;
  background-color: #06283d;
  color: whitesmoke;
  border: 1px solid rgb(83, 6, 6);
  border-radius: 8px;
  cursor: pointer;
}
.btn-outline {
  background-color: #06283d;
  color: white;
  text-align: center;
}
.center {
  text-align: center;
  margin: 24px 0;
}
p {
  margin: 10px 0;
}
.img-container {
  text-align: center;
}
.img-container img {
  height: 200px;
}
